import React, {useState, useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useParams, useNavigate } from 'react-router-dom';
import { Row, Col, Image, ListGroup, Card, Button, Form, ListGroupItem } from 'react-bootstrap';
import Rating from '../components/Rating';
import Loader from '../components/Loader';
import Message from '../components/Message';
import { listProductDetails, createProductReview } from '../actions/productActions'
import { PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants'


function ProductScreen({ match }) {
    const history = useNavigate();
    const { id } = useParams();
    const [qty, setQty] = useState(1)
    const [rating, setRating] = useState(0)
    const [comment, setComment] = useState('')


    const dispatch = useDispatch()

    const productDetails = useSelector(state => state.productDetails)
    const { loading, error, product } = productDetails

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    const productReviewCreate = useSelector(state => state.productReviewCreate)
    const { 
        loading: loadingProductReview,
        error: errorProductReview,
        success: successProductReview, 
    } = productReviewCreate

    useEffect(()=>{
        if(successProductReview) {
            setRating(0)
            setComment('')
            dispatch({type: PRODUCT_CREATE_REVIEW_RESET})
        }

        dispatch(listProductDetails(id))

    }, [dispatch, match, successProductReview])

    const addToCartHandler = () =>{
        history(`/cart/${id}?qty=${qty}`)
    }

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(createProductReview(
            id, {
                rating,
                comment
            }
        ))
    }

  return (
    <div>
      <Link to="/" className="btn btn-light my-3">Go Back</Link>
      {loading ? <Loader />
        : error ? <Message variant='danger'>{error}</Message>
            : (
                <div>
                <Row>
                    <Col md={6}>
                        <iframe src={product.ai_bot_url} width='100%' height='400px' allow='microphone' allowFullScreen> 
                        {/* <Link to={product.short_description}> */}
                        <div style={{ position: 'relative' }}>
                            <Image
                                src={product.image}
                                alt={product.name}
                                style={{ width: '700px', height: '500px', objectFit: 'cover'}}
                                fluid
                                />
                            <div style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', textAlign: 'center' }}>
                                <h3 style={{ color: 'white', fontSize: '24px', textShadow: '2px 2px 4px #000000' }}>Try me</h3>
                            </div>
                        </div>
                        {/* </Link> */}
                        </iframe>
                    </Col>

                    <Col md={6}>
                        <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <h3>{product.name}</h3>
                            </ListGroup.Item>

                            {/* <ListGroup.Item>
                                <Rating value={product.rating} text={`${product.numReviews} reviews`} color={'#f8e825'} />
                            </ListGroup.Item> */}

                            {/* <ListGroup.Item>
                                Price: ${product.price}
                            </ListGroup.Item> */}

                            <ListGroup.Item style={{ textAlign: 'justify' }}>
                                Description: {product.description}
                            </ListGroup.Item>
                        </ListGroup>

                    </Col>

                    {/* <Col md={3}>
                    <Card>
                        <ListGroup variant='flush'>
                            <ListGroup.Item>
                                <Row>
                                    <Col>
                                        Price:
                                    </Col>
                                    <Col>
                                        <strong>${product.price}</strong>
                                    </Col>
                                </Row>
                            </ListGroup.Item>

                            <ListGroup.Item>
                                <Row>
                                    <Col>
                                        Status:
                                    </Col>
                                    <Col>
                                        {product.countInStock > 0 ? 'In Stock' : 'Out of Stock'}
                                    </Col>
                                </Row>
                            </ListGroup.Item>

                            {product.countInStock > 0 && (
                                <ListGroup.Item>
                                    <Row>
                                        <Col>Qty</Col>
                                        <Col xs='auto' className='my-1'>
                                            <Form.Control
                                                as='select'
                                                value={qty}
                                                onChange={(e) => setQty(e.target.value)}
                                            >
                                                {
                                                    [...Array(product.countInStock).keys()].map((x) =>(
                                                        <option key={x + 1} value={x + 1}>
                                                            {x + 1}
                                                        </option>
                                                    ))
                                                }
                                            </Form.Control>
                                        </Col>
                                    </Row>
                                </ListGroup.Item>
                            )}

                            <ListGroup.Item>
                                <Button onClick={addToCartHandler} style={{ width: '100%', height: '100%' }} disabled={product.countInStock === 0} type='button'>Add to Card</Button>
                            </ListGroup.Item>
                        </ListGroup>
                    </Card>
                    </Col> */}
                </Row>

                <Row>
                    <Col md={6}>
                        <h4 style={{ marginTop: '20px', marginLeft: '20px' }}>Reviews</h4>
                        <div style={{ marginLeft: '20px' }}>
                        {product.reviews.length === 0 && <Message variant='info' >No Reviews</Message>}

                        <ListGroup variant='flush'>
                            {product.reviews.map((review) => (
                                <ListGroup.Item key={review._id}>
                                    <strong>{review.name}</strong>
                                    <Rating value={review.rating} color='#f8e825' />
                                    <p>{review.createAt.substring(0,10)}</p>
                                    <p>{review.comment}</p>
                                </ListGroup.Item>
                            ))}

                            <ListGroup.Item>
                                <h5>Write a Review</h5>

                                {loadingProductReview && <Loader />}
                                {successProductReview && <Message variant='success'>Review submitted</Message>}
                                {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}

                                {userInfo ? (
                                    <Form onSubmit={submitHandler}>
                                        <Form.Group controlId='rating'>
                                            <Form.Label>Rating</Form.Label>
                                            <Form.Control
                                                as='select'
                                                value={rating}
                                                onChange={(e) => setRating(e.target.value)}
                                            >
                                                <option value=''>Select...</option>
                                                <option value='1'>1 - Poor</option>
                                                <option value='2'>2 - Fair</option>
                                                <option value='3'>3 - Good</option>
                                                <option value='4'>4 - Very Good</option>
                                                <option value='5'>5 - Excellent</option>
                                            </Form.Control>
                                        </Form.Group>

                                        <Form.Group controlId='comment'>
                                            <Form.Label>Review</Form.Label>
                                            <Form.Control
                                                as='textarea'
                                                row='5'
                                                value={comment}
                                                onChange={(e) => setComment(e.target.value)}
                                            >
                                            </Form.Control>
                                        </Form.Group>

                                        <Button
                                            disabled={loadingProductReview}
                                            type='submit'
                                            variant='primary'
                                        >
                                            Submit
                                        </Button>

                                    </Form>
                                ) : (
                                    <Message variant='info'>Please <Link to='/login'>Login</Link> to write a review</Message>
                                )}
                            </ListGroup.Item>
                        </ListGroup>
                        </div>
                    </Col>
                </Row>
                </div>
            )
      }
    </div>
  );
}

export default ProductScreen
